.nav-bg {
    width: 100%;
    height: 55px;
    border-bottom: solid 1px #80808038;
    position: relative;
}

.site-title {
    margin-left: 15px;
    color: #d42903;
    width: 110px;
}

.price-feeds {
    position: absolute;
    top: 40px;
    left: 20px;
    font-size: 9px;
}

.site-settings-icon {
    width: 30px;
    height: 30px;
    background-color: gray;
    position: absolute;
    right: 180px;
    top: 12px;
    cursor: pointer;
}

.settings-container {
    width: 90%;
    height: 80%;
    position: fixed;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 10;
    background-color: white;
    box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.1);
}

.settings-chain-c {
    width: 90%;
    max-width: 600px;
    height: 170px;
    border: solid 2px gray;
    border-radius: 5px;
    margin: 0 auto;
    margin-top: 20px;
}

.settings-chain {

}

.settings-chain-option {
    width: 70%;
    max-width: 400px;
    text-align: center;
    background-color: gray;
    margin: 0 auto;
    margin-top: 15px;
    padding: 10px;
    color: white;
    border-radius: 10px;
    font-weight: bold;
    cursor: pointer;
}

.menu-icon {
    border-radius: 8px;
    padding: 5px 10px;
    color: white;
    background-color: black;
    position: absolute;
    right: 15px;
    top: 10px;
    cursor: pointer;
}
